      .col-sm-12.col-md-12.col-lg-9
        p En el desarrollo del producto es necesario analizar las funciones del patronista;  se identifican necesidades como el manejo y dominio bsico por operacin de confeccin o ensamble al momento de elaborar cualquier tipo de prenda o producto, si bien la destreza en el manejo y dominio de mquinas industriales de confeccin hace parte del desarrollo integral del patronista, es importante tener claro que todo molde, patrn, plano o desarrollo de diseo que se realiza debe ir acompaado de una ruta u orden operacional, ficha tcnica de produccin con especificaciones para el ensamble, las cuales van de la mano con el rea de prototipado o muestreo, en donde es indispensable y fundamental el conocimiento de un patronista, motivo por el cual en el tcnico se enfatiza en la importancia del conocimiento y desarrollo por operacin de las prendas de vestir.
        p Dentro del proceso y desarrollo del producto se identifican elementos como aditamentos, pies, guas y flderes, los cuales permiten tener mejores resultados en el proceso de confeccin porque aumentan la eficiencia y calidad en las prendas, por ejemplo, para el desarrollo de productos en las diversas operaciones que se requieran en la mquina plana, de manera frecuente se presenta el cambio de pies. 

