        #t_3_1.titulo-segundo.color-primario
          h2 3.1 Ruta u orden operacional para camisa  
        p Cuando se habla de ruta u orden operacionales corresponde al paso a paso por operacin y con tiempos preestablecidos, desarrollados en una determinada mquina, equipo o de manera manual, con el fin de obtener una prenda terminada, para el caso se cita la camisa. Las operaciones de confeccin corresponden al desarrollo de movimientos y mtodos de costura en la mquina, equipos o exclusivamente manuales, en donde se van uniendo las piezas que componen la prenda para dar como resultado una prenda completamente confeccionada.
        p Para la confeccin de la camisa se requiere realizar operaciones segn la clasificacin analizada, es decir, operaciones de preparacin (iniciales, unir piezas por aparte), ensamble, el cual consiste en unir delantero y posterior anteriormente preparado y finalmente las operaciones de terminacin, las cuales son especializadas antes del embalaje como por ejemplo colocar botones, planchar o revisar. 
