    h3 A. Preparacin de las piezas
    p.mt-4 Dentro del proceso de ensamble, rutas operacionales y en algunas maquilas, antes de pasar al proceso operacional en las mquinas se hace un proceso de plantillado fusionado y prehormado, utilizando planchas industriales o equipos de fusionado,  con el fin de agilizar los procesos de produccin o los tiempos de la operacin, a nivel de produccin se tiene una operaria encargada de hacer solo una operacin, es decir, una sola persona se encarga de fusionar, otra se encarga de planchar y otra de marcar o plantillar bolsillos, pecheras y dems complementos de la prenda.   
    p.mb-4 Todas estas operaciones se realizan despus del proceso de corte. 

