        h2 3.3 Ensamble de falda
      p Dentro de las operaciones de inicio para la confeccin de una falda se debe analizar una de las operaciones crticas, esta operacin es el sistema de cierre con cremallera. Adems, es importante realizar el alistamiento de los recursos a necesitar: los hilos y sus caractersticas, la cremallera, las piezas cortadas en tela y entretela, las agujas, las guas y los dems elementos que aseguren el flujo de las operaciones.
      p Para el desarrollo de la ruta operacional en confeccin de falda se inicia con las operaciones de preparacin, luego se desarrolla el ensamble al unir el posterior y delantero por los costados o laterales, y finaliza con las operaciones de terminados como ojalar, botonar y planchar. 

